<template>
    <div id="radar" ref="radar" :style="{ width: '600px', height: '400px' }"></div>
</template>

<script>
import * as echarts from "echarts";
import {getRequest,postRequest} from '@/api/user'

export default {
    name: "Radar",
    data(){
        return {
            personData:[], 
            
        }
    },
    methods: {
        initInfo(){
          // 
          getRequest("/ten/data/personData").then((res)=>{
            this.personData = res.data;
            console.log("personData2");
            console.log(this.personData);
            console.log("personData2");

            this.drawLine();
          }).catch(error=>{
            console.log(error);
          })
        },
          

        drawLine() {
            console.log("radar", this.personData);
        // domecharts
        let radar = this.$echarts.init(document.getElementById('radar'));
        var option={
            title:{
                text:''
            },
            tooltip:{
                trigger:'axis'
            },
            legend: {
                data: ['','', '']
            },
            polar:[{
                indicator:[//,data value 
                        { text: '', max: 20},
                        { text: '', max: 300},// ,
                        { text: '', max: 200},
                        { text: '', max: 100},
                        { text: '', max: 900},
                        { text: '', max: 90},
                        { text: '', max: 2.5},
                        
                        { text: '', max: 120}
                    ]}],
            calculable:true,
            series:[
                {
                    name:'',
                    symbol: 'none',//
                    center: ['50%', '50%'],// [ default: ['50%', '50%'] ]
                    radius:160,// 
                    itemStyle:{normal:{areaStyle:{type:'default'}}},
                    type:'radar',
                    data:[
                        {value:[0,0,60,11.3,0,18.5, 0, 0], name:''},
                        {value:this.personData, name:''},
                        {value:[2 ,199.8,80,13.7,0,24,2.42, 81], name:''}
                        ]
            }]
        };
        radar.setOption(option);

        }
    },
    
    mounted() {
        this.initInfo();
        
    },
 


}

</script>
