<template>
  <div>
    <!--==========================          ==============================================================-->
    <div id="top_buttons">
      <div id="load_state">
        <span></span>
        <el-select v-model="value1" placeholder="">
          <el-option
            v-for="item in options_up"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>

      <div id="data_source">
        <span></span>
        <el-select v-model="value2" placeholder="" >
          <el-option
            v-for="item in options_source"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <el-button @click="clearFilter" size="small"></el-button>
      <div id="top_right_buttons">
        <el-button type="primary" @click="importData"></el-button>
      </div>
    </div>

    <!--===============================         ==============================================================-->
    <div id="table">
      <el-table
        :data="
          tableData.filter(
            (data) =>
              !(value1 || value2) ||
              (data?.tableStatus?.includes(value1) &&
                data?.tableOrigin?.includes(value2))
          )
        "
        style="width: 100%"
        :row-style="{ height: '33px', lineHeight: '10px', padding: '0px' }"
        :header-cell-style="{
          background: '#58AAFF',
          color: '#fff',
          lineHeight: '12px',
          padding: '0px',
          height: '34px',
          textAlign: 'center',
        }"
      >
        <el-table-column label="" prop="tableName"> </el-table-column>
        <el-table-column label="" prop="tableOrigin"> </el-table-column>
        <el-table-column label="" prop="disease"> </el-table-column>
        <el-table-column label="" prop="tableSize"> </el-table-column>
        <el-table-column label="" prop="tablePeople"> </el-table-column>
        <el-table-column label="" prop="tableDate"> </el-table-column>
        <el-table-column align="center">
          <template slot="header">
            <span></span>
          </template>
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              style="background: #1976d2; border: #1976d2"
              @click="handleDelete(scope.row.tableName)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--===============================        ===================================================================-->
    <el-dialog
      v-loading="loading"
      :element-loading-text="loadText"
      id="importDataTable"
      title=""
      :visible.sync="dialogFormVisible"
      width="40%"
    >
      <el-form
        :model="dialogForm"
        ref="dialogFormRef"
        :rules="dialogForm.rules"
        label-width="110px"
      >
        <el-form-item label="" prop="filesInfo">
          <el-upload
            action=""
            class="upload"
            ref="uploadRef"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :on-change="changeFile"
            :auto-upload="false"
            accept=".csv"
            :limit="1"
            :multiple="false"
            :file-list="dialogForm.filesInfo"
            :http-request="
              (data) => {
                upRequest(data);
              }
            "
          >
            <el-button slot="trigger" size="small" type="success"
              ></el-button
            >
            <div slot="tip" class="el-upload__tip">csv</div>
          </el-upload>
        </el-form-item>

        <el-form-item label="" prop="tableName">
          <el-input
            v-model="dialogForm.tableName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="dataDisease">
          <el-select
            v-model="dialogForm.dataDisease"
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in disOptions"
              :key="item.name"
              :label="item.name"
              :value="item.name"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button @click="resetForm('dialogFormRef')"></el-button>
        <el-button type="primary" @click="uploadFile"></el-button>
      </div>

      <el-dialog
        append-to-body
        title=""
        :visible.sync="featuresVision"
      >
        <el-form class="featureLabel" label-width="auto">
          <el-form-item
            v-for="(name, index) in Object.keys(featuresMap)"
            :key="index"
            :label="name"
          >
            <el-select
              v-model="featuresMap[name]"
              placeholder=""
              @input="e=>{test(e,name)}"
            >
              <el-option
                label=""
                value="people"
                key="people"
              ></el-option>
              <el-option
                label=""
                value="social"
                key="social"
              ></el-option>
              <el-option
                label=""
                value="medical"
                key="medical"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="compelete"></el-button>
        </div>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
import { postRequest } from "@/utils/api";
import { getRequest } from "@/api/user";
import { mapGetters, mapMutations, mapState, mapActions } from "vuex";
import { disOptions } from "@/components/mixins/constData.js";
import { resetForm, debounce } from "@/components/mixins/mixin";
import axios from 'axios'
import VueAxios from "vue-axios";
import Vue from "vue";

export default {
  mixins: [resetForm, debounce],
  computed: {
    ...mapGetters(["dataDisList", "dataCreatorList"]),
    ...mapState(["dataList"]),
  },

  watch: {
    "dialogForm.tableName"() {
      this.checkTableName();
    },
  },

  data() {
    return {
      loading: false,
      loadText: "",
      disease: "",
      creator: "",
      disOptions,
      tableData: [],
      featuresVision: false,
      featuresMap: {
      },
      options_up: [
        //
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
      value1: "",
      options_source: [
        //
        {
          value: "CSV",
          label: "CSV",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "MySQL",
          label: "MySQL",
        },
        {
          value: "EXCEL",
          label: "EXCEL",
        },
        {
          value: "TXT",
          label: "TXT",
        },
      ],
      value2: "",
      dialogForm: {
        filesInfo: [],
        tableName: "",
        isOnly: true,
        dataDisease: "",
        featuresNum: 1,
        fields: [{ name: "", type: "" }],
        rules: {
          tableName: [
            {
              required: true,
              message: "",
              trigger: "change",
            },
          ],
          dataDisease: [
            {
              required: true,
              message: "",
              trigger: "blur",
            },
          ],
          numFeatures: [
            { required: true, message: "", trigger: "blur" },
            { type: "integer", message: "", trigger: "blur" },
            { min: 1, message: ">1", trigger: "blur" },
          ],
        },
      },
      dialogFormVisible: false,
    };
  },

  created() {
    // 

    this.checkTableName = this.debounce(() => {
      console.log('' + this.dialogForm.tableName)
      getRequest("/DataTable/inspection", {
        "name": this.dialogForm.tableName,
      }).then((res) => {
        // 
        console.log(res)
        if (!this.dialogForm.isOnly && res) {
          this.$message({
            showClose: true,
            message: "",
            type: "success",
          });
        }
        if (typeof res === "boolean") {
          this.dialogForm.isOnly = res;
        }
        if (!res) {
          this.$message({
            showClose: true,
            message: "",
            type: "warning",
          });
          return false;
        }
        return true;
      });
    }, 800);
  },
  mounted() {
    this.getAllData();
  },

  methods: {
    ...mapMutations(["SetDataList"]),
    ...mapActions(["getDataList"]),
    getAllData() {
      let tempList;
      getRequest("/DataTable/upall", {}).then((res) => {
        tempList = res;
        for (let i = 0; i < tempList.length; i++) {
          const obj = {
            tableName: tempList[i].tableName,
            tablePeople: tempList[i].creator,
            tableOrigin: tempList[i].tableDesc,
            tableSize: (tempList[i].samplesize).toString(10) + '' + (tempList[i].featurenumber).toString(10),
            tableDate: tempList[i].createTime,
            tableStatus: tempList[i].tableStatus,
            disease: tempList[i].disease,
          };
          // console.log("obj = " + obj);
          console.log("tableName = " + tempList[i].table_name);
          this.tableData.push(obj);
        }
      });
      // let te = this.tableData;
      // console.log(te)



      // let tempList = JSON.parse(this.$store.getters.getAllTableData);

      // for (let i = 0; i < tempList.length; i++) {
      //   const obj = {
      //     tableName: tempList[i].tableName,
      //     tablePeople: tempList[i].tablePeople,
      //     tableOrigin: tempList[i].tableOrigin,
      //     tableSize: tempList[i].tableSize,
      //     tableDate: tempList[i].startTime,
      //     tableStatus: tempList[i].tableStatus,
      //   };
      //   this.tableData.push(obj);
      // }

    },
    clearFilter() {
  
      this.value1 = "";
      this.value2 = "";
    },
    test(e, index) {
      const obj = {
        ...this.featuresMap
      };
      obj[index] = e;
      this.featuresMap = obj;
  
    },


    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(tableName) {
      console.log(tableName);
      postRequest(`DataTable/delete/${tableName}`).then((res) => {
        this.SetDataList(res);
      });
    },
  
    importData() {
      this.dialogFormVisible = true;
    },

    handleSubmit() {
      console.log("...");
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    beforeUpload(file) {
      //
      const isCSV =
        file.type === "text/csv" || file.type === "application/vnd.ms-excel";
      // const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isCSV) {
        this.$message.error(" CSV ");
      }
      // if (!isLt2M) {
      //     this.$message.error(' 2MB!');
      // }
      // return isJPG && isLt2M;
      return isCSV;
    },

    removeFileExtension(fileName) {
      // 
      const lastDotIndex = fileName.lastIndexOf(".");

      // 
      if (
        lastDotIndex !== -1 &&
        lastDotIndex < fileName.length - 1 &&
        lastDotIndex > 0
      ) {
        // 
        return fileName.substring(0, lastDotIndex);
      } else {
        // 
        return fileName;
      }
    },

    changeFile() {
      console.log(this.$refs["uploadRef"].uploadFiles);
      if (this.dialogForm.tableName.length < 1) {
        this.dialogForm.tableName = this.removeFileExtension(
          this.$refs["uploadRef"].uploadFiles[0].name
        );
      }
    },

    upRequest(data) {
      const payload = new FormData();
      payload.append("file", data.file);
      console.log(data.file);
      payload.append("newName", this.dialogForm.tableName);
      payload.append("disease", this.dialogForm.dataDisease);
      payload.append("createName", sessionStorage.getItem("user"),);
      console.log("")
      const options = {
        method: "post",
        data: payload,
        url: "/DataTable/upload",
        headers: {
          "Content-Type": "multipart/form-data",
        },
      };
      Vue.use(VueAxios, axios)
      Vue.prototype.$axios = axios;
      this.$axios(options).then((res) => {
        this.loading = false;
        console.log(res);
        if (res?.code == "200") {
          this.$message({
            showClose: true,
            type: "success",
            message: "",
          });
          let featureList = res.tableHeaders;

          // map
          for (const item of featureList) {
            // this.featuresMap.set(item, "people");
            this.featuresMap[item] = "people";
          }
          console.log(this.featuresMap);
          this.featuresVision = true;
        } else {
          this.$message({
            showClose: true,
            type: "error",
            message: "",
          });
        }
      });
    },

    uploadFile() {
      if (this.$refs["uploadRef"].uploadFiles.length < 1) {
        this.$message({
          showClose: true,
          type: "warning",
          message: "",
        });
        return false;
      }
      this.checkTableName();
      if (!this.dialogForm.isOnly) {
        return false;
      }
      this.$refs["dialogFormRef"].validate((valid) => {
        if (valid) {
          this.loadText = "";
          this.loading = true;
          this.$refs.uploadRef.submit();
        }
      });
    },

    compelete() {
      this.loadText = "";
      this.loading = true;
      let tableHeaders = [];
      console.log("compeletefeaturesMap.id", this.featuresMap.id);
      for (const key in this.featuresMap) {
        console.log("old", key, this.featuresMap[key]);
        if (Object.hasOwnProperty.call(this.featuresMap, key)) {
          console.log("new", key, this.featuresMap[key]);
          switch (this.featuresMap[key]) {
            case "people":
              tableHeaders.push({
                fieldName: key,
                isDemography: "1",
                isPhysiological: "0",
                isSociology: "0",
              });
              break;
            case "medical":
              tableHeaders.push({
                fieldName: key,
                isDemography: "0",
                isPhysiological: "1",
                isSociology: "0",
              });
              break;
            case "social":
              tableHeaders.push({
                fieldName: key,
                isDemography: "0",
                isPhysiological: "0",
                isSociology: "1",
              });
              break;
            default:
              break;
          }
        }
      }

      postRequest("/tTableManager/insertTableManager", {
        tableName: this.dialogForm.tableName,
        tableHeaders,
      }).then((res) => {
        console.log(res);
        // if(res.length>this.dataList.length){
        // resvuexdatalistbug
        // this.SetDataList(res);
        this.getDataList();
        this.$message({
          showClose: true,
          type: "success",
          message: "",
        });
        this.featuresVision = false;
        this.dialogFormVisible = false;
        // }
      });
    },
  },
};
</script>

<style scoped>
#top_buttons > * {
  display: inline-block;
}
#DiseaseFilter {
  margin-right: 40px;
}
#creatorFilter {
  margin-right: 20px;
}
